import Pop from '@/components/pop.vue'
export default {
	data() {
		return {
			name: '',
			sex: 3,
			characterStr: '',
			constellationStr: '',
			constellatory: '',
			abc: [],
			popType: '1',
			feature: ''
		}
	},
	onLoad() {

		swan.setPageInfo({
			title: '起英文名_起名网_八字起名_周易取名_取名字_起名字大全',
			keywords: '起英文名,起乳名,起名字,取名字,宝宝起名,八字起名,周易起名,诗经起名,男孩起名,女孩起名',
			description: '起名网官方取英文名小程序，提供宝宝起名、男孩取名、女孩起名、周易起名、八字起名、诗经起名等智能起名字取名字服务。',
			articleTitle: '起英文名,取乳名,起名字,取名字,宝宝起名,八字起名,周易起名,诗经起名,男孩起名,女孩起名',
			releaseDate: '2019-05-15 12:01:30',
			image: '',
			video: '',
			visit: '',
			likes: '',
			comments: '',
			collects: '',
			shares: '',
			followers: '',
			success: function() {
				console.log('页面基础信息设置完成');
			}
		})

		for (var i = 0; i < 26; i++) {
			this.abc.push(String.fromCharCode((65 + i)));
		}
	},
	components: {
		Pop
	},
	methods: {
		submit() {
			let data = {
				surname: this.name, //true	string	姓名
				sex: this.sex, //true	int	性别1为女性，3为男性
				feature: this.characterStr, //true	string	选择的特征id
				constellatory: this.constellatory, //true	string	选择的星座
				app: 1
			}
			if(data.surname==''){
				uni.showToast({
					title:'请输入姓名',
					icon: 'none'
				})
				return
			}
			if(data.feature==''){
				uni.showToast({
					title:'请选择标签',
					icon: 'none'
				})
				return
			}
			if(data.constellatory==''){
				uni.showToast({
					title:'请选择星座',
					icon: 'none'
				})
				return
			}
			if(data.surname==''){
				uni.showToast({
					title:'请输入姓名',
					icon: 'none'
				})
				return
			}
			uni.showLoading({
				title: '起名中'
			});
			uni.request({
				url: this.$store.state.baseUrl + '/api/english/query',
				data: data,
				method: 'POST',
				header: {
					"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
				},
				success: (res) => {
					uni.hideLoading()
					if(res.data.code==1){
							uni.setStorageSync('english_name',res.data.data)
						uni.navigateTo({
							url: '../list/list'
						})
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						})
					}
					
				}
			})
		},
		characterPop() {
			this.popType = '2'
			this.$refs.pop.show()
		},
		constellationPop() {
			this.popType = '1'
			this.$refs.pop.show()
		},
		popVal(obj) {
			if (this.popType == 1) {
				this.constellationStr = obj.text
				this.constellatory = obj.value
			} else {
				this.characterStr = obj.text
				this.feature = obj.value
			}
		},
		linkWeb(item) {
			uni.navigateTo({
				url: '../webview/webview?type=' + item
			})
		}
	}
}
